@import 'variables';
@import 'normalize';
@import 'mixins';
@import 'fonts';
@import 'components/container';
@import 'components/footer';

.container{
    width: calc(100% - 102px);
    margin: 0 auto;
    max-width: none;
    padding: 0;
}

.header{
    padding: 18px 0 19px 0;
    border-bottom: 1px solid #1E2E36;

    &__row{
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include  md{
            flex-wrap: wrap;
        }

        @include  sm{
            display: block;
        }
    }

    &__info{
        display: flex;
        align-items: center;

        @include  sm{
            margin-bottom: 10px;
        }

        @include  xs{
            display: block;
        }
    }

    &__text{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        margin-right: 13px;
    }

    &__icons{
        display: flex;
        align-items: center;
        margin-right: 55px;
    }

    &__icon{
        margin-right: 30px;
    }

    &__phone{
        display: flex;
        align-items: center;
    }

    &__phone-icon{
        margin-right: 10px;
    }

    &__phone-num{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 22px;
        text-transform: uppercase;
        color: #1E2E36;
        margin-right: 10px;
    }

    &__drop-down{
        cursor: pointer;
    }

    &__nav{
        display: flex;

        @include  lg{
            flex-wrap: wrap;
        }

        &-item{
            font-family: "ProximaNova";
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            text-transform: uppercase;
            margin-right: 15px;

            @include  lg{
                margin-bottom: 10px;
            }
            
            a{
                color: #1E2E36;
            }

            &:last-of-type{
                margin-right: 0;
            }

            &_active{
                border-bottom: 3px solid #44C697;
            }
        }
    }
}

.main-logo{
    padding: 15px 0;

    &__row{
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include  md{
            display: block;
        }
    }

    &__assortment{
        display: flex;
        align-items: center;

        @include  lg{
            flex-wrap: wrap;
        }

        @include  md{
            justify-content: center;
            margin-bottom: 10px;
        }

        &-item{
            margin-right: 20px;
            display: flex;
            align-items: center;

            @include  lg{
                margin-bottom: 10px;
            }

            &:last-of-type{
                margin-right: 0;
            }

            &-link{
                font-family: "ProximaNova";
                font-style: normal;
                font-weight: bold;
                font-size: 18px;
                line-height: 22px;
                text-transform: uppercase;
                color: #1E2E36;
                margin-right: 7px;
            }
        }

        &-drop-down{
            width: 9.5px;
            height: 13.5px;
        }

    }

    &__logo{
        @include  md{
            margin-bottom: 10px;
        }

        &-img{
            @include  md{
                margin: 0 auto;
            }
        }
        
    }

    &__order{
        display: flex;
        align-items: center;

        @include  lg{
            flex-wrap: wrap;
        }

        @include  md{
           justify-content: center;
        }

        &-items{
            display: flex;
            align-items: center;

            &:first-of-type{
                margin-right: 30px;
            }
        }

        &-icon{
            margin-right: 5px;
        }

        &-text{
            font-family: "ProximaNova";
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            text-transform: uppercase;
            color: #1E2E36;
        }

        &-btn{
            display: flex;
            align-items: center;
            justify-content: center;
            font-family: "ProximaNova";
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            text-transform: uppercase;
            color: #FFFFFF;
            padding: 14px 46px;
            background-color: #44C697;
            border-radius: 3px;
            border: none;
        }

    }
}

.novelty{
    height: 785px;
    background-image: url("../img/novelty-img.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    margin-bottom: 100px;

    @include  lg{
        height: 700px;
    }

    @include  md{
        height: 600px;
    }

    @include  sm{
        height: 500px;
    }

    &__block{
        position: absolute;
        background-color: #FFFFFF;
        width: 462px;
        left: 51px;
        padding: 99px 10px 99px 10px;
        text-align: center;
        border-left: 15px solid #44C697;
        box-sizing: border-box;

        @include  lg{
            padding: 80px 10px 80px 10px;
        }

        @include  md{
            padding: 50px 10px 50px 10px;
            width: 400px;
        }

        @include  sm{
            padding: 10px 10px 10px 10px;
            left: 5px;
            width: 300px;
        }
    }

    &__title{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 64px;
        line-height: 64px;
        color: #1E2E36;
        margin-bottom: 50px;

        @include  md{
            font-size: 36px;
            line-height: 45px;
            margin-bottom: 25px;
        }
    }

    &__title-h2{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 36px;
        line-height: 45px;
        color: #1E2E36;
        margin-bottom: 10px;

        @include  md{
            font-size: 18px;
            line-height: 22px;
            margin-bottom: 5px;
        }
    }

    &__text{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: normal;
        font-size: 36px;
        line-height: 45px;
        color: #1E2E36;
        margin-bottom: 30px;

        @include  md{
            font-size: 18px;
            line-height: 22px;
            margin-bottom: 15px;
        }
    }

    &__btn{
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 22px;
        color: #FFFFFF;
        padding: 14px 46px;
        background-color: #44C697;
        border-radius: 3px;
        border: none;
        margin: 0 auto 50px;
    }

    &__counter{
        width: max-content;
        margin: 0 auto;
    }

    &__num{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 36px;
        line-height: 45px;
        color: #1E2E36;
        margin-bottom: 5px;

        span{
            font-size: 24px;
            line-height: 30px;
        }
    }

    &__icons{
        display: flex;
        justify-content: center;
    }

    &__icon{
        cursor: pointer;

        &:first-of-type{
            margin-right: 8px;
        }
    }
}

.preference{
    margin-bottom: 100px;

    &__title{
        position: relative;
        width: max-content;
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 48px;
        line-height: 60px;
        color: #1E2E36;
        margin: 0 auto 50px;

        @include  md{
            font-size: 36px;
            line-height: 45px;
        }

        &::before{
            content: "";
            position: absolute;
            height: 50px;
            width: 20px;
            background-color: #44C697;
            z-index: -1;
            left: -5px;
            top: 5px;

            @include  md{
                height: 35px;
            }
        }
    }

    &__items{
        position: relative;
        display: flex;
        justify-content: center;

        @include  md{
            flex-wrap: wrap;
        }

        &::before{
            content: url("../img/preference-left.svg");
            position: absolute;
            left: 15px;
            top: 50%;
            transform: translateY(-50%);
            cursor: pointer;

            @include  md{
                display: none;
            }
        }

        &::after{
            content: url("../img/preference-right.svg");
            position: absolute;
            right: 15px;
            top: 50%;
            transform: translateY(-50%);
            cursor: pointer;

            @include  md{
                display: none;
            }
        }
    }

    &__item{
        display: flex;
        width: 33%;
        flex-direction: column;
        justify-content: end;
        cursor: pointer;
        height: 350px;
        margin-right: 30px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        border-radius: 3px;
        padding: 30px;

        @include  lg{
            height: 300px;
        }

        @include  md{
            width: 40%;
            margin-bottom: 10px;
        }

        @include  sm{
            width: 90%;
            margin-right: 0;
        }

        &:last-of-type{
            margin-right: 0;
        }

        &-title{
            font-family: "ProximaNova";
            font-style: normal;
            font-weight: bold;
            font-size: 36px;
            line-height: 45px;
            color: #FFFFFF;
            border-bottom: 3px solid #44C697;

            @include  lg{
                font-size: 30px;
                line-height: 39px;
            }
        }

        &:hover .preference__text{
            display: block;
        }
    }

    &__text{
        display: none;
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 22px;
        color: #FFFFFF;
        margin-top: 15px;
    }

}

.categories{

    margin-bottom: 100px;

    &__title{
        position: relative;
        width: max-content;
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 48px;
        line-height: 60px;
        color: #1E2E36;
        margin: 0 auto 50px;

        @include  md{
            font-size: 36px;
            line-height: 45px;
        }

        &::before{
            content: "";
            position: absolute;
            height: 50px;
            width: 20px;
            background-color: #44C697;
            z-index: -1;
            left: -5px;
            top: 5px;

            @include  md{
                height: 35px;
            }
        }
    }

    &__icons{
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin-bottom: 87px;

        @include  md{
            flex-wrap: wrap;
            margin-bottom: 50px;
        }
    }

    &__icon{
        cursor: pointer;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 140px;

        @include  md{
            margin-right: 30px;
            margin-bottom: 20px;
        }
    }

    &__icon-title{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 30px;
        text-align: center;
        text-transform: uppercase;
    }

    &__icon_active{
        .categories__img-element{
            fill: #44C697;
        }

        .categories__icon-title{
            border-bottom: 3px solid #44C697;
        }

    }
    
    &__items{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 50px 30px;
        margin-bottom: 50px;

        @include  md{
            grid-template-columns: repeat(2, 1fr);
        }

        @include  sm{
            grid-template-columns: repeat(1, 1fr);
        }
    }

    &__item{
        position: relative;
        cursor: pointer;
    }

    &__item-info{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 15px;
    }

    &__item-title{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 30px;
        text-transform: uppercase;
        color: #1E2E36;

        @include  lg{
            font-size: 18px;
            line-height: 22px;
        }
    }

    &__item-price-block{
        display: flex;
        align-items: center;
    }

    &__item-price{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 30px;
        text-transform: uppercase;
        color: #1E2E36;

        @include  lg{
            font-size: 18px;
            line-height: 22px;
        }
    }

    &__item-price-through{
        display: none;
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 22px;
        text-transform: uppercase;
        text-decoration: line-through;
        margin-left: 10px;
        color: #BCBDBB;

        @include  lg{
            font-size: 14px;
            line-height: 18px;
        }
    }

    &__item_action{

        &::after{
            content: "Акція";
            position: absolute;
            left: 0;
            top: 50px;
            background-color: #FF1B1B;
            padding: 5px 15px;
            font-family: "ProximaNova";
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            color: #FFFFFF;
            text-transform: uppercase;
        }

       .categories__item-price{
           color: #FF1B1B;
       } 

       .categories__item-price-through{
           display: block;
       }
    }

    &__item_new{

        &::before{
            content: "Новинка";
            position: absolute;
            left: 0;
            top: 10px;
            background-color: #1E2E36;
            padding: 5px 15px;
            font-family: "ProximaNova";
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            color: #FFFFFF;
            text-transform: uppercase;
        }
    }

    &__item_top{

        &::before{
            content: "ХІт продаж";
            position: absolute;
            left: 0;
            top: 10px;
            background-color: #1E2E36;
            padding: 5px 15px;
            font-family: "ProximaNova";
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            color: #FFFFFF;
            text-transform: uppercase;
        }
    }

    &__item{
        &:hover{
            transform: translateY(-25px);
            
            .categories__item-info{
                background-color: #44C697;
            }

            .categories__item-title{
                color: #FFFFFF;
            }

            .categories__item-price{
                color: #FFFFFF;
            }

            .categories__item-price-through{
                color: #FFFFFF;
            }
        }
    }

    &__btn{
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 22px;
        text-transform: uppercase;
        color: #FFFFFF;
        background-color: #44C697;
        padding: 14px 46px;
        margin: 0 auto;
        border: none;
        border-radius: 3px;
    }
}

.about-us{

    margin-bottom: 100px;

    &__title{
        position: relative;
        width: max-content;
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 48px;
        line-height: 60px;
        color: #1E2E36;
        margin: 0 auto 50px;

        @include  md{
            font-size: 36px;
            line-height: 45px;
        }

        &::before{
            content: "";
            position: absolute;
            height: 50px;
            width: 20px;
            background-color: #44C697;
            z-index: -1;
            left: -5px;
            top: 5px;

            @include  md{
                height: 35px;
            }
        }
    }

    &__items{
        display: flex;
        justify-content: end;
        position: relative;

        @include  md{
            display: block;
        }
    }

    &__text{
        position: absolute;
        padding: 50px 30px 50px 51px;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        background-color: #FFFFFF;
        border-right: 15px solid #44C697;
        border-radius: 3px;

        @include  lg{
            padding: 25px 15px 25px 51px;
        }

        @include  md{
            padding: 10px 10px 10px 51px;
            transform: translateY(0);
            position: static;
        }
    }

    &__paragraph{
        width: 710px;
        margin-bottom: 15px;
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 22px;
        color: #1E2E36;

        @include  lg{
            width: 610px;
            font-size: 14px;
            line-height: 18px;
        }

        @include  md{
            width: 100%;
            font-size: 14px;
            line-height: 18px;
        }


        &:last-of-type{
            margin-bottom: 0;
        }

        span{
            font-weight: bold;
            color: #44C697;
            text-transform: uppercase;
        }
    }

    &__img-block{
        @include  md{
            display: none;
        }
    }
}

.question{

    margin-bottom: 109px;

    &__title{
        position: relative;
        width: max-content;
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 48px;
        line-height: 60px;
        color: #1E2E36;
