        }
    }

    &__form-title{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 36px;
        line-height: 45px;
        color: #1E2E36;
        margin-bottom: 30px;

        @include  md{
            font-size: 30px;
            line-height: 39px;
        }
    }

    &__form{
        width: 432px;
        margin-bottom: 30px;

        @include  md{
            width: 90%;
        }
    }

    &__input{
        width: 100%;
        height: 51px;
        margin-bottom: 15px;
        padding: 14px 15px;
