.footer{

    display: grid;
    grid-template-columns: max-content 1fr;
    background-color: #FFFFFF;
    min-height: 0;
    padding: 0;

    @include  md{
        grid-template-columns: 100% 0;
    }

    &__info-block{
        position: relative;
        background-color: #1E2E36;
        padding: 50px 50px 15px 51px;

        @include  md{
            padding: 50px 20px 15px 10px;
        }

        &::before{
            content: url("../img/btn-to-top.svg");
            position: absolute;
            left: 30px;
            top: -20px;
            cursor: pointer;
        }
    }

    &__logo{
        margin-bottom: 50px;
        width: 360px;
        height: 75px;

        @include  md{
            width: 300px;
        }
    }

    &__info{
        position: relative;
        display: flex;

        @include  md{
            flex-wrap: wrap;
        }
    }

    &__list-item{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 22px;
        color: #FFFFFF;
        text-transform: uppercase;
        margin-bottom: 15px;

        &:last-of-type{
            margin-bottom: 0;
        }

        span{
            color: #44C697;
        }
    }

    &__column{

        @include  md{
            margin-bottom: 15px;
        }

        &:first-of-type{  
            margin-right: 55px;
        }

        &:nth-of-type(2){
            margin-right: 80px;
        }
    }

    &__test-block{
        display: flex;
        align-items: center;
        margin-bottom: 15px;
    }

    &__test-icon{
        margin-right: 7px;
    }

    &__test-text{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 22px;
        color: #FFFFFF;
    }

    &__schedule-block{
        display: flex;
        align-items: center;
        margin-bottom: 15px;
    }

    &__schedule-icon{
        margin-right: 7px;
    }

    &__schedule-text{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 22px;
        color: #FFFFFF;
    }

    &__phone-block{
        display: flex;
        align-items: flex-start;
        margin-bottom: 30px;
    }

    &__phone-icon{
        margin-left: -3px;
        margin-right: 7px;
    }

    &__phone-num{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 22px;
        text-transform: uppercase;
        color: #44C697;

        &:first-of-type{
            margin-bottom: 5px;
        }
    }

    &__social-block{
        margin-bottom: 100px;

        @include  md{
            margin-bottom: 25px;
        }
    }

    &__social-text{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 22px;
        color: #FFFFFF;
        margin-bottom: 15px;
    }

    &__social-icons{
        display: flex;
        align-items: center;
    }

    &__social-icon{
        margin-right: 30px;

        &:last-of-type{
            margin-right: 0;
        }
    }


    &__copy-block{
        position: absolute;
        bottom: 0;
    }

    &__copy-text{
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
    }

    &__bottom-block{
        display: flex;
        align-items: center;

        @include  md{
            margin-bottom: 10px;
        }
    }

    &__bottom-text{
        margin-left: 3px;
        font-family: "ProximaNova";
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 16px;
        text-transform: uppercase;
        color: #FFFFFF;
    }

    &__img-block{
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;

        @include  md{
            display: none;
        }
    }
}